.hero_darkModeInfo {
  position: absolute;
  right: 2%;
  top: 2%;
  font-size: 0.7rem;
}

.hero__darkMode {
  z-index: 1;
  position: absolute;
  cursor: pointer;
  right: 3%;
  top: 5%;
  width: 40px;
  height: 20px;
  border-radius: 20px;
  background-color: rgb(205, 208, 211);
  transition: 0.5s;
  box-shadow: inset 0 8px 60px rgba(255, 255, 255, 0.1), inset 0 8px 8px rgba(255, 255, 255, 0.1), inset 0 -4px -4px rgba(255, 255, 255, 0.1);
}
.hero__darkMode--insideIcon {
  position: absolute;
  right: 3%;
  width: 20px;
  height: 20px;
  background: linear-gradient(to bottom, #444, #fff);
  border-radius: 50%;
  transform: scale(0.9);
  box-shadow: 0 8px 40px rgba(255, 255, 255, 0.5), inset 0 4px 4px rgba(0, 0, 0, 0.2), inset 0 -4px 4px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}

.hero__darkMode.active .hero__darkMode--insideIcon {
  right: 20px;
}

.dark {
  background-color: #421d35;
  background-image: linear-gradient(225deg, #421d35 0%, #573c71 50%, #133a55 100%);
  color: #b3a3ad;
}
.dark .hero__conteiner--btn {
  color: #b3a3ad;
  background-color: #421d35;
}
.dark .hero__conteiner--img {
  opacity: 0.7;
}
@media (min-width: 230px) and (max-width: 480px) {
  .dark .hero__conteiner--img {
    display: none;
  }
}
.dark .hero__popup, .dark .hero__darkMode.active {
  background-color: #6a3056;
  background-image: linear-gradient(225deg, #6a3056 0%, #634e76 50%, #35556b 100%);
}